const theForLoop = () => {
    const moods = ['Ecstatic', 'Happy', 'meh', 'Upset'];
    for (let i = 0; i < moods.length; i++){
        console.log("I am: ", moods[i]);
    }
}

const theForEachLoop = () => {
    
    const moods = ['Ecstatic', 'Happy', 'meh', 'Upset'];

    moods.forEach(mood => console.log(mood))
    
}

const theMapHelper = () => {
    const teams = [
        {
            name: 'Manchester United',
            coach: 'Ole Gunnar Soklsjear',
            country: "England",
            wins: 5,
            losses: 2,
            draws: 1            
        },
        {
            name: 'Bayern Munich',
            coach: 'Julian Nagelsmann',
            country: "Germany",
            wins: 6,
            losses: 1,
            draws: 1
        },
        {
            name: 'Orlando City',
            coach: 'Oscar Pareja',
            country: "United States of America",
            wins: 4,
            losses: 2,
            draws: 2
        },
    ]

    const matchWinners = teams.map(team => team.wins += 1);

    console.log({ teams });
    console.log({ matchWinners });
}

const theFilterHelper = () => {
    
    const teams = [
    
        {
            name: 'Manchester United',
            coach: 'Ole Gunnar Soklsjear',
            country: "England",
            wins: 5,
            losses: 2,
            draws: 1            
        },
        {
            name: 'Bayern Munich',
            coach: 'Julian Nagelsmann',
            country: "Germany",
            wins: 6,
            losses: 1,
            draws: 1
        },
            {
                name: 'Orlando City',
                coach: 'Oscar Pareja',
                country: "United States of America",
                wins: 4,
                losses: 2,
                draws: 2
            },
    ]
    
    const notUSATeams = teams.filter(team => team.country !== 'United States of America');

    console.log({ teams });
    console.log({ notUSATeams });

}



const theReduceHelper = () => {
    const matchResults = ['win', 'win', 'win', 'draw', 'loss', 'win', 'win', 'win', 'draw', 'win'];

    const seasonResults = matchResults.reduce((match, result) => {
        match[result] = (match[result] || 0) + 1;
        return match;
    
    }, {})
    
    console.log({ seasonResults });
}

export {
    theForLoop,
    theForEachLoop,
    theMapHelper,
    theFilterHelper,
    theReduceHelper
}

